<template>
    <div>
        <el-container>
            <el-header>
                tlias 智能学习辅助系统
            </el-header>
        <el-container>

            <el-aside width="220px" style="border : 1px solid #eee">
                <el-menu :default-openeds="['1', '3']">
                    <el-submenu index="1">
                        <template slot="title"><i class="el-icon-message"></i>系统信息管理</template>
                        <el-menu-item index="1-1">
                            <router-link to="/dept">部门管理</router-link>
                        </el-menu-item>
                        <el-menu-item index="1-2">
                            <router-link to="/emp">员工管理</router-link>
                        </el-menu-item>
                    </el-submenu>
                </el-menu>
            </el-aside>

            <el-main>

                <template>
                    <el-table
                        :data="tableData"
                        style="width: 100%"
                        :row-class-name="tableRowClassName">
                        <el-table-column prop="number" label="序号" width="250">
                        </el-table-column>
                        <el-table-column prop="dept" label="部门" width="250">
                        </el-table-column>
                        <el-table-column prop="count" label="人数" width="250">
                        </el-table-column>
                        <el-table-column prop="status" label="状态" width="250">
                        </el-table-column>
                        <el-table-column prop="updatetime" label="最后操作时间" width="250">
                        </el-table-column>
                        <el-table-column label="操作" width="250">
                        <el-button type="primary" size="mini">编辑</el-button>
                        <el-button type="danger" size="mini">删除</el-button>
                        </el-table-column>
                    </el-table>
                </template>

            </el-main>
        </el-container>
        </el-container>
    </div>
</template>

<script>

import axios from 'axios';

export default {
    methods: {
      tableRowClassName({rowIndex}) {
        if (rowIndex === 1) {
          return 'warning-row';
        } else if (rowIndex === 3) {
          return 'success-row';
        }
        return '';
      }
    },
    data() {
      return {
        tableData: []
      }
    },
    mounted () {
        axios.get('http://127.0.0.1:4523/m1/6353946-6049592-default/dept/').then((result) => {
            this.tableData = result.data.data;
        });
    }
}
</script>

<style>
     .el-table .warning-row {
    background: oldlace;
  }

  .el-table .success-row {
    background: #f0f9eb;
  }
</style>